// 
import WebGLUtils from './webgl-utils';
import WebGLDebugUtils from './webgl-debug';
import {WebGLRenderingContextWithProgram} from "../types/index";

/**
 *  gl 
 * @param {WebGLRenderingContextWithProgram} gl WebGL 
 * @param {string} vshader 
 * @param {string} fshader 
 * @return void
 */
export function initShaders(gl: WebGLRenderingContextWithProgram, vshader: string, fshader: string): void {
  const program = createProgram(gl, vshader, fshader);
  gl.useProgram(program);
  gl.program = program;
}

/**
 * 
 * @param {WebGLRenderingContextWithProgram} gl WebGL 
 * @param {WebGLProgram} program 
 * @return void
 * */
export function useProgram(gl: WebGLRenderingContextWithProgram, program: WebGLProgram): void {
  gl.useProgram(program);
  gl.program = program;
}

/**
 *  program 
 * @param {WebGLRenderingContextWithProgram} gl WebGL 
 * @param {string} vshader 
 * @param {string} fshader 
 * @return WebGLProgram
 */
export function createProgram(gl: WebGLRenderingContextWithProgram, vshader: string, fshader: string): WebGLProgram {
  // 
  const vertexShader = loadShader(gl, gl.VERTEX_SHADER, vshader);
  const fragmentShader = loadShader(gl, gl.FRAGMENT_SHADER, fshader);

  // 
  const program = gl.createProgram();
  if (!program) {
    throw new Error('');
  }

  // 
  gl.attachShader(program, vertexShader);
  gl.attachShader(program, fragmentShader);

  // gl 
  gl.linkProgram(program);

  // 
  const linked = gl.getProgramParameter(program, gl.LINK_STATUS);
  if (!linked) {
    gl.deleteProgram(program);
    gl.deleteShader(fragmentShader);
    gl.deleteShader(vertexShader);
    throw new Error(`${gl.getProgramInfoLog(program)}`);
  }
  return program;
}

/**
 * 
 * @param {WebGLRenderingContextWithProgram} gl WebGL 
 * @param {GLenum} type /
 * @param {string} source 
 * @return created shader object, or null if the creation has failed.
 */
function loadShader(gl: WebGLRenderingContextWithProgram, type: GLenum, source: string): WebGLShader {
  // 
  const shader = gl.createShader(type);
  if (shader === null) {
    throw new Error(` ${type} `);
  }

  // 
  gl.shaderSource(shader, source);

  // 
  gl.compileShader(shader);

  // 
  const compiled = gl.getShaderParameter(shader, gl.COMPILE_STATUS);
  if (!compiled) {
    gl.deleteShader(shader);
    throw new Error(`${type}${gl.getShaderInfoLog(shader)}`);
  }

  return shader;
}

/**
 * WebGL
 * @param {HTMLCanvasElement} canvas canvas 
 * @param {boolean} [debug]  debug
 * @return WebGLRenderingContextWithProgram 
 */
export function getWebGLContext(canvas: HTMLCanvasElement, debug?: boolean): WebGLRenderingContextWithProgram {
  // @ts-ignore  WebGL 
  let gl = WebGLUtils.setupWebGL(canvas, {
    // 
    antialias: true,
  });
  if (!gl) {
    throw new Error(' WebGL ');
  }

  //  debug
  if (debug) {
    // @ts-ignore
    gl = WebGLDebugUtils.makeDebugContext(gl);
  }

  return gl;
}
