//  WebGL 

import {WebGLRenderingContextWithProgram} from "../types/index";

/**
 *  TypedArray 
 * @param {WebGLRenderingContextWithProgram} gl WebGL 
 * @param {Float32Array} data 
 * @param {number} size  data vertexAttribPointer 
 * @param {GLenum} type  WebGL 
 * @param {string} attribute 
 * */
export function initArrayBuffer(
  gl: WebGLRenderingContextWithProgram,
  data: Float32Array,
  size: number,
  type: GLenum,
  attribute: string
): void {
  const buffer = gl.createBuffer();
  if (!buffer) {
    throw new Error('');
  }
  gl.bindBuffer(gl.ARRAY_BUFFER, buffer);
  gl.bufferData(gl.ARRAY_BUFFER, data, gl.STATIC_DRAW);
  const a_attribute = gl.getAttribLocation(gl.program, attribute);
  if (a_attribute < 0) {
    throw new Error(` ${attribute} `);
  }
  gl.vertexAttribPointer(a_attribute, size, type, false, 0, 0);
  gl.enableVertexAttribArray(a_attribute);
}

/**
 * 
 * @param {string} src 
 * @return Promise<TexImageSource> 
 * */
export function loadImage(src: string): Promise<TexImageSource> {
  return new Promise((resolve, reject) => {
    const image = new Image();
    image.crossOrigin = 'anonymous';
    image.onload = () => resolve(image);
    image.onerror = error => reject(error);
    image.src = src;
  });
}
